<template>
  <div id="app">
    <navbar></navbar>
    <router-view/>
  </div>
</template>
<script>
import '@/assets/font-awesome/css/font-awesome.min.css'
import Navbar from '@/components/Navbar'
import Chart from 'chart.js';
import Vue from 'vue'
import VueGoogleCharts from 'vue-google-charts'

Vue.use(VueGoogleCharts)

export default {
  name: 'App',
   components: {
    'navbar': Navbar
  }
}
</script>

<style>
div#hoverSwap span#a {
  display: inline;
  cursor: pointer;
}

div#hoverSwap:hover span#a {
  display: none;
  cursor: pointer;
}

div#hoverSwap span#b {
  display: none;
  cursor: pointer;
}

div#hoverSwap:hover span#b {
  display: inline;
  font-size: 14px;
  cursor: pointer;
}
#services_background_color
#body-row {
    margin-left:0;
    margin-right:0;
}
#sidebar-container {
    min-height: 100vh;   
    background-color: #333;
    padding: 0;
}

/* Sidebar sizes when expanded and expanded */
.sidebar-expanded {
    width: 230px;
}
.sidebar-collapsed {
    width: 60px;
}

/* Menu item*/
#sidebar-container .list-group a {
    height: 50px;
    color: white;
}

/* Submenu item*/
#sidebar-container .list-group .sidebar-submenu a {
    height: 45px;
    padding-left: 30px;
}
.sidebar-submenu {
    font-size: 0.9rem;
}

/* Separators */
.sidebar-separator-title {
    background-color: #333;
    height: 35px;
}
.sidebar-separator {
    background-color: #333;
    height: 25px;
}
.logo-separator {
    background-color: #333;    
    height: 60px;
}

/* Closed submenu icon */
#sidebar-container .list-group .list-group-item[aria-expanded="false"] .submenu-icon::after {
  content: " \f0d7";
  font-family: FontAwesome;
  display: inline;
  text-align: right;
  padding-left: 10px;
}
/* Opened submenu icon */
#sidebar-container .list-group .list-group-item[aria-expanded="true"] .submenu-icon::after {
  content: " \f0da";
  font-family: FontAwesome;
  display: inline;
  text-align: right;
  padding-left: 10px;
}
/* -------------------------------------------------------- */
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
.sidebar_nav_item{
  background-color: white; color: grey; border-radius: 5px; border: 1px solid grey;
}
.sidebar_nav_item:hover {
  background-color: grey; color: white; border-radius: 5px;
}
.sign_out {
  height: 30px; margin-top: 25px; margin-right: 30px; padding: 3px 15px 5px 15px; font-size: 14px; background-color: grey; color: white; border-color: grey;
}
.sign_out:hover {
  height: 30px; margin-top: 25px; margin-right: 30px; padding: 3px 15px 5px 15px; font-size: 14px; background-color: white; color: grey;
}
.latency_btn {
  background-color: white; color: grey; height: 23px; font-size: 14px; padding: 3px 5px 0px 5px; border: 1px solid lightgray; font-size: 11px;
}
#summary_details_btn {
  background-color: white; border: 1px solid #323a45; color: #323a45 !important; cursor:pointer; border-radius: 5px;
}
#summary_details_btn:hover {
  background-color: white; border: 1px solid #e6e7e8; color: white;
}

.background-card {
  background-color: white !important;
}
</style>



